/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  font-family: "Raleway", sans-serif;
  color: #FFFFFF;
}

.container {
  max-width: 64rem;
  padding: 0 1.25rem;
  margin: 0 auto;
}

.text-center {
  text-align: center;
}

.hero {
  background: url("../../assets/header-360.png") no-repeat center/cover;
}
@media (min-width: 40em) {
  .hero {
    background: url("../../assets/header-1024.png") no-repeat center/cover;
  }
}
@media (min-width: 64em) {
  .hero {
    background: url("../../assets/header-1920.png") no-repeat center/cover;
  }
}
.hero__content {
  padding-top: 53px;
  padding-bottom: 195px;
}
@media (min-width: 40em) {
  .hero__content {
    padding-top: 128px;
    padding-bottom: 83px;
  }
}
.hero__logo {
  width: 138px;
  padding-bottom: 103px;
}
@media (min-width: 40em) {
  .hero__logo {
    width: 252px;
  }
}
.hero__headline {
  font-size: 2.8125rem;
  font-weight: bold;
  padding-bottom: 3px;
}
@media (min-width: 64em) {
  .hero__headline {
    font-size: 5.4375rem;
  }
}
.hero__text {
  font-size: 1.125rem;
}
@media (min-width: 64em) {
  .hero__text {
    font-size: 30px;
  }
}

.list {
  background-color: #7A2B58;
  padding-top: 105px;
  padding-bottom: 62px;
}
.list__box {
  border: 3px solid #FFFFFF;
  border-radius: 20px;
}
.list__content {
  padding: 20px;
}
.list__image {
  position: absolute;
  top: 495px;
  left: 100px;
}
.list__headline {
  color: #FFCC33;
  font-size: 2rem;
  font-weight: bold;
  text-align: center;
  padding-top: 104px;
  padding-bottom: 40px;
}
.list__icon {
  position: absolute;
  width: 107px;
}